import {
  JSONType,
  FeatureFlagType,
  ConfigurationDataType,
} from '../campaignConfiguration';
import { Environment, ExternalPackageInfoType } from '../types';
import { Header } from './constants';

export type RequesterParamsType = {
  siteCode: string;
  environment?: Environment;
  packageInfo: ExternalPackageInfoType;
  requestDispatcher: IExternalRequestDispatcher;
};

export type TrackExperimentParamsType = {
  body: string;
  variationId: number;
  visitorCode: string;
  experimentId: number;
  isUnallocated: boolean;
  userAgent?: string;
  callback?: () => void;
};

export type TrackDataParamsType = {
  visitorCode: string;
  body: string;
  userAgent?: string;
};

export type GetClientConfigurationResultType = Omit<
  ConfigurationDataType,
  'featureFlags'
> & { featureFlagConfigurations: FeatureFlagType[] };

export enum HttpMethod {
  Get = 'GET',
  Post = 'POST',
}

const EVENT_TYPE_KEYS = [
  'customData',
  'staticData',
  'page',
  'conversion',
  'activity',
  'experiment',
] as const;
const PARAMETER_KEYS = [
  'valuesCountMap',
  'id',
  'nonce',
  'sdkName',
  'sdkVersion',
  'browserIndex',
  'siteCode',
  'environment',
  'visitorCode',
  'negative',
  'revenue',
  'title',
  'referrersIndices',
  'overwrite',
  'index',
  'href',
  'variationId',
  'deviceType',
  'goalId',
  'ts',
  'key',
] as const;
const TRACKING_KEYS = ['visit', 'map'] as const;
const QUERY_KEYS = ['sse', 'mobile', 'map', 'events'] as const;

export type TrackingType = Record<
  Capitalize<(typeof TRACKING_KEYS)[number]>,
  string
>;
export type QueryType = Record<Capitalize<(typeof QUERY_KEYS)[number]>, string>;
export type ParameterType = Record<
  Capitalize<(typeof PARAMETER_KEYS)[number]>,
  string
>;
export type EventType = Record<
  Capitalize<(typeof EVENT_TYPE_KEYS)[number]>,
  string
>;

export type StrictTrackingRecord<T> = {
  [K in keyof T]: K extends string ? `${Uncapitalize<K>}/` : never;
};
export type StrictQueryRecord<T> = {
  [K in keyof T]: K extends string ? `${Uncapitalize<K>}?siteCode=` : never;
};
export type StrictEventTypeRecord<T> = {
  [K in keyof T]: K extends string ? `eventType=${Uncapitalize<K>}` : never;
};
export type StrictParameterRecord<T> = {
  [K in keyof T]: K extends string ? `&${Uncapitalize<K>}=` : never;
};

export type TrackParametersType = {
  url: string;
  headers?: Partial<Record<Header, string>>;
  body: string;
};

/**
 * @interface an interface of External Request Dispatcher which will be used to perform requests.
 * */
export interface IExternalRequestDispatcher {
  /**
   * @method track - send a track request
   * @param {TrackParametersType} parameters - track parameters
   * */
  track: (params: TrackParametersType) => Promise<boolean>;
  /**
   * @method get - get data
   * @param {string} url - url address
   * */
  get: (url: string) => Promise<JSONType>;
}
